export function protocolDateFormat(date) {
  let dd = "";
  let mm = "";
  let yy = "";

  if (date) {
    const d = new Date(date);

    dd = d.getDate();
    if (dd < 10) {
      dd = "0" + dd;
    }
    mm = d.getMonth() + 1;

    switch (mm) {
      case 1:
        mm = "";
        break;
      case 2:
        mm = "";
        break;
      case 3:
        mm = "";
        break;
      case 4:
        mm = "";
        break;
      case 5:
        mm = "";
        break;
      case 6:
        mm = "";
        break;
      case 7:
        mm = "";
        break;
      case 8:
        mm = "";
        break;
      case 9:
        mm = "";
        break;
      case 10:
        mm = "";
        break;
      case 11:
        mm = "";
        break;
      case 12:
        mm = "";
        break;
    }

    yy = d.getFullYear();
  }

  return "" + dd + "" + " " + mm + " " + yy + " .";
}
